import React from 'react';

const PlayerCard = ({ player, isDraggable = false }) => {
  return (
    <div className={`player-card ${isDraggable ? 'draggable' : ''}`} draggable={isDraggable}>
      <div className="player-header">
        <span className="player-name">{player.nickname}</span>
        <span className="player-game-id">{player.game_id}</span>
      </div>
      <div className="player-info">
        <div className="player-info-item">
          <span className="player-info-label">群昵称</span>
          <span className="player-info-value">{player.group_nickname}</span>
        </div>
        <div className="player-info-item">
          <span className="player-info-label">天梯分数</span>
          <span className="player-info-value score-value">{player.score}</span>
        </div>
        <div className="player-info-item">
          <span className="player-info-label">胜率</span>
          <span className="player-info-value">{player.win_rate}%</span>
        </div>
        <div className="player-info-item">
          <span className="player-info-label">冠军</span>
          <span className="player-info-value">{player.championships}</span>
        </div>
      </div>
      <div className="position-tags">
        {player.positions.map((pos, index) => (
          <span key={index} className="position-tag">{pos}</span>
        ))}
      </div>
      <div className="heroes-list">
        {player.heroes.map((hero, index) => (
          <span key={index} className="hero-tag">{hero}</span>
        ))}
      </div>
    </div>
  );
};

export default PlayerCard;
import React from 'react';

const TeamCard = ({ team, onAddPlayer, onRemoveTeam, onRemovePlayer }) => {
  const handleRemoveTeam = () => {
    if (window.confirm(`确定要删除 ${team.name} 吗？`)) {
      onRemoveTeam(team.id);
    }
  };

  const handleRemovePlayer = (playerId) => {
    if (window.confirm('确定要将该选手移回未分配池吗？')) {
      onRemovePlayer(team.id, playerId);
    }
  };

  const totalScore = team.players.reduce((sum, player) => sum + player.score, 0);

  return (
    <div className="team-card">
      <div className="team-header">
        <div className="team-info">
          <h3>{team.name} (ID: {team.id})</h3>
          <div className="team-stats">
            总天梯分数: <span className="team-score">{totalScore}</span>
            <span className="team-player-count">{team.players.length}/5人</span>
          </div>
        </div>
        <div className="team-actions">
          <button className="remove-team-btn" onClick={handleRemoveTeam}>删除队伍</button>
        </div>
      </div>
      <div className="team-players">
        {team.players.length === 0 ? (
          <div className="empty-state">暂无选手</div>
        ) : (
          team.players.map(player => (
            <div key={player.id} className="player-card simplified">
              <div className="player-header">
                <span className="player-name">{player.nickname}</span>
                <span className="player-game-id">{player.game_id}</span>
              </div>
              <div className="player-info simplified-info">
                <div className="player-info-item">
                  <span className="player-info-label">群昵称</span>
                  <span className="player-info-value">{player.group_nickname}</span>
                </div>
                <div className="player-info-item">
                  <span className="player-info-label">天梯分数</span>
                  <span className="player-info-value score-value">{player.score}</span>
                </div>
                <div className="player-info-item">
                  <span className="player-info-label">冠军</span>
                  <span className="player-info-value">{player.championships}</span>
                </div>
              </div>
              <button 
                className="remove-player-btn" 
                onClick={() => handleRemovePlayer(player.id)}
              >
                移除
              </button>
            </div>
          ))
        )}
      </div>
      <button 
        className="add-player-btn" 
        onClick={() => onAddPlayer(team.id)}
      >
        + 添加选手
      </button>
    </div>
  );
};

export default TeamCard;
import Head from 'next/head';
import { useState, useEffect } from 'react';
import PlayerCard from '../components/PlayerCard';
import TeamCard from '../components/TeamCard';

export default function Home() {
  const [players, setPlayers] = useState([]);
  const [teams, setTeams] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);

  useEffect(() => {
    const fetchData = async () => {
      try {
        const [playersRes, teamsRes] = await Promise.all([
          fetch('/api/players'),
          fetch('/api/teams')
        ]);

        if (!playersRes.ok || !teamsRes.ok) {
          throw new Error('Failed to fetch data');
        }

        const playersData = await playersRes.json();
        const teamsData = await teamsRes.json();

        setPlayers(playersData.players || []);
        setTeams(teamsData.teams || []);
        setLoading(false);
      } catch (err) {
        setError(err.message);
        setLoading(false);
      }
    };

    fetchData();
  }, []);

  const handleCreateTeam = async () => {
    try {
      const res = await fetch('/api/teams', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json'
        },
        body: JSON.stringify({ name: `队伍${teams.length + 1}` })
      });

      if (!res.ok) {
        throw new Error('Failed to create team');
      }

      const { team } = await res.json();
      setTeams([...teams, team]);
    } catch (err) {
      alert(`创建队伍失败: ${err.message}`);
    }
  };


  const handleAddPlayer = async (teamId) => {
    // TODO: 实现添加选手到队伍的功能
    console.log(`Adding player to team ${teamId}`);
  };

  const handleRemovePlayer = async (teamId, playerId) => {
    // TODO: 实现从队伍移除选手的功能
    console.log(`Removing player ${playerId} from team ${teamId}`);
  };

  if (loading) {
    return <div className="loading">加载中...</div>;
  }

  if (error) {
    return (
      <div className="error">
        <h2>加载失败</h2>
        <p>{error}</p>
        <button onClick={() => window.location.reload()}>重新加载</button>
      </div>
    );
  }

  return (
    <div className="container">
      <header>
        <h1>选手分配系统</h1>
        <button className="create-team-btn" onClick={handleCreateTeam}>
          创建新队伍
        </button>
      </header>

      <main>
        <section className="players-section">
          <h2>所有选手 ({players.length})</h2>
          <div className="players-grid">
            {players.map(player => (
              <PlayerCard 
                key={player.id} 
                player={player} 
                isDraggable={true}
              />
            ))}
          </div>
        </section>

        <section className="teams-section">
          <h2>队伍列表 ({teams.length})</h2>
          <div className="teams-grid">
            {teams.map(team => (
              <TeamCard
                key={team.id}
                team={team}
                onAddPlayer={handleAddPlayer}
                onRemoveTeam={handleRemoveTeam}
                onRemovePlayer={handleRemovePlayer}
              />
            ))}
          </div>
          {teams.length === 0 && (
            <div className="empty-state">
              <p>暂无队伍</p>
              <button onClick={handleCreateTeam}>创建第一个队伍</button>
            </div>
          )}
        </section>
      </main>
    </div>
  );
}
